.react-app-container body {
  font-family: "Roboto", sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f0f0f0;
}

.react-app-container canvas {
  display: block;
  background-color: #f0f0f0;
  border: 1px solid #ccc;
}

.react-app-container .gutter {
  background-color: #ccc;
  background-repeat: no-repeat;
  background-position: center;
}

.react-app-container .gutter.gutter-horizontal {
  cursor: col-resize;
}

.gutter {
  background-color: #ddd;
  cursor: col-resize;
  width: 12px;
  transition: background-color 0.2s ease;
}

.gutter:hover {
  background-color: #bbb;
}

/* In case your react-split passes a direction class */
.gutter-horizontal {
  /* Adjust height if needed */
  height: auto;
}
